let memoized_worker;

onmessage = function(e) {
  if (e.data.init) {
    import('w-mc')
      .then((wasm) => {
        memoized_worker = wasm
        postMessage(e.data.i)
      })
      .catch(err => console.error("Error importing .wasm:", err));
  } else {
    let res = memoized_worker.simjs(e.data)
    postMessage(res)
  }
}